import { Fragment } from "react";
import "./Cart.css";
import { useDispatch, useSelector } from "react-redux";
import {
  addItemsToCart,
  removeItemsFromCart,
} from "../../redux/actions/cartAction";
import Button from "../ReusableComponent/Button/Button";
import { MdRemoveShoppingCart } from "react-icons/md";
import CartItemCard from "./CartItem/CartItemCard";
import { useAlert } from "react-alert";
import { Link, useNavigate } from "react-router-dom";

const Cart = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const alert = useAlert();
  const { cartItems } = useSelector((state) => state.cart);

  const increaseQuantity = (id, quantity, stock) => {
    if (stock <= quantity) {
      return;
    }
    const newQty = quantity + 1;
    dispatch(addItemsToCart(id, newQty));
  };

  const decreaseQuantity = (id, quantity) => {
    if (1 >= quantity) {
      return;
    }
    const newQty = quantity - 1;
    dispatch(addItemsToCart(id, newQty));
  };

  const deleteCartItemCard = (id) => {
    dispatch(removeItemsFromCart(id));
    alert.success("Item successfully Removed From the Cart");
  };

  const checkoutHandler =()=>{
    navigate("/login?redirect=shipping")
  }
  return (
    <Fragment>
      {cartItems.length > 0 ? (
        <div className="cartPage">
          <div className="cartHeader">
            <p>Product</p>
            <p>Quantity</p>
            <p>Sub-Total</p>
          </div>
          <div className="cartItems">
            {cartItems &&
              cartItems.map((item) => (
                <div className="cartContainer" key={item.product}>
                  <CartItemCard
                    item={item}
                    deleteCartItemCard={deleteCartItemCard}
                  />
                  <div className="cartInput quantityBtn">
                    <button
                      onClick={() =>
                        decreaseQuantity(item.product, item.quantity)
                      }
                    >
                      -
                    </button>
                    <input readOnly type="number" value={item.quantity} />
                    <button
                      onClick={() =>
                        increaseQuantity(
                          item.product,
                          item.quantity,
                          item.stock
                        )
                      }
                    >
                      +
                    </button>
                  </div>
                  <p className="cartSubTotal">₹{item.price * item.quantity}</p>
                </div>
              ))}
          </div>

          <div className="cartGrossProfit">
            <div></div>
            <div className="cartGrossProfitBox">
              <p>Gross Total</p>
              <p>
              ₹{cartItems.reduce(
                  (intialVal, nextItem) =>
                    intialVal + nextItem.quantity * nextItem.price,
                  0
                )}
              </p>
            </div>
            <div></div>
            <div className="checkOutBtn">
              <Button onClick={checkoutHandler}>Check Out</Button>
            </div>
          </div>
        </div>
      ) : (
        <div className="emptyCart">
          <MdRemoveShoppingCart />
          <p>No Products In Your Cart</p>
          <Link to="/products">View Products</Link>
        </div>
      )}
    </Fragment>
  );
};

export default Cart;
